<template>
    <div style="border: 1px solid grey; padding-bottom: 10px;" :style="{'height':height+'px'}">
        <div :ref="this.$data.tool_id" class="editor-bar" style="background-color: #6a6a6a3f"></div>
        <div :ref="this.$data.editor_id" :style="{'height': height-40+'px'}"> </div>
    </div>
</template>

<script type="js">
    import E from 'wangeditor'
    import md5 from 'js-md5'
    export default {
        name: 'editor',
        props:{
            index: null,
            height: Number
        },
        data () {
            return {
                uploadPath: this.$urls.uploadPath,
                res: md5(new Date().toUTCString()),
                tool_id: 'editor'+ this.res,
                editor_id : 'toolbar' + this.res
            }
        },
        methods: {
            loadEditor: function () {
                let editor = new E(this.$refs[this.tool_id], this.$refs[this.editor_id])
                editor.customConfig.uploadImgServer = this.$urls.uploadPath;
                editor.customConfig.onchange = (html) => {
                    this.$emit('change', html, this.index);
                }
                editor.create()
            },
            show: function () {
                console.log("click")
            }

        },
        mounted() {
            this.loadEditor();
        }
    }
</script>

<style lang="scss">
    .w-e-text-container{
        padding-bottom: 2px;
    }
    .w-e-menu{
        z-index: 98 !important;
    }
    .w-e-text-container{
        z-index: 97 !important;
    }
</style>
